import { Component, OnInit, ViewChild } from "@angular/core";
import { ReportsService } from "../_service/reports.service";
import { AuthenticationService } from "../../../../_services/authentication.service";
import * as moment from "jalali-moment";
import { FormControl } from "@angular/forms";
import { SharedService } from "../../../../_services/shared.service";
import { DaterangeComponent } from '../_components/daterange/daterange.component';

@Component({
  selector: "app-all",
  templateUrl: "./all.component.html",
  styleUrls: ["./all.component.scss"]
})
export class AllComponent implements OnInit {
  constructor(
    private reportServ: ReportsService,
    private authServe: AuthenticationService,
    private sharedService: SharedService
  ) {}

  public inPerformanceLabel: string[]= ['  ', ' '] ;;
  public inPerformanceColors = [
    {
      backgroundColor: ["#4dbd74", "#eeeeee"]
    }
  ];

  public inPercentsColors = [
    {
      backgroundColor: ["#a5deb9", "#f86c6b", '#ffda6a']
    }
  ];
  
  public barChartInColors = [
    {
      backgroundColor: "#d1d1d1",
      borderColor: "#d1d1d1",
      borderWidth: 1
    },
    {
      backgroundColor: "#4dbd74",
      borderColor: "#4dbd74",
      borderWidth: 1
    },
    {
      backgroundColor: "#e32234",
      borderColor: "#e32234",
      borderWidth: 1
    },
    {
      backgroundColor: "#ffc107",
      borderColor: "#ffc107",
      borderWidth: 1
    }
  ];

  public barChartOutColors = [
    {
      backgroundColor: "#d1d1d1",
      borderColor: "#d1d1d1",
      borderWidth: 1
    },
    {
      backgroundColor: "#009688",
      borderColor: "#009688",
      borderWidth: 1
    },
    {
      backgroundColor: "#673ab7",
      borderColor: "#673ab7",
      borderWidth: 1
    },
    {
      backgroundColor: "#795548",
      borderColor: "#795548",
      borderWidth: 1
    }
  ];

  public inPerformanceData: number[] = [1, 100];

  public inDetailsPercent: number[] = [1,0,0];
  public outDetailsPercent: number[] = [1,0,0];

  allData: any = [];
  globData: any = [];

  @ViewChild('daterange') dateRange : DaterangeComponent;

  onSelectDate() {
   // this.updateCharts();
  }

  ngOnInit() {
  }


  loadingData = false;
  updateCharts() {
    this.loadingData = true;
    let data = {
      from: this.dateRange.selectedDateFrom.value,
      to: this.dateRange.selectedDateTo.value
    };
    this.reportServ.getSystemPerformance(data).subscribe(
      data => {
        data = data["data"];
        this.allData = data;

        this.barChartDataIn = [
          { data: [data["in"]["all"]], label: "  " },
          { data: [data["in"]["answer"]], label: "   " },
          { data: [data["in"]["noanswer"]], label: "   " },
          { data: [data["in"]["busy"]], label: " " }
        ];

        this.inDetailsPercent = [data["in"]['panswer'], data["in"]['pnoanswer'], data["in"]['pbusy']];
        this.outDetailsPercent = [data["out"]['pbetweenco'], data["out"]['pco'], data["out"]['pmobile']];

        this.barChartDataTimeIn = [
          { data: [data['in']["time"]], label: "   " },
          { data:  [data['in']["avg"]], label: "    " },
        ];

        this.barChartDataTimeOut = [
          { data: [data['out']["time"]], label: "   " },
          { data:  [data['out']["avg"]], label: "    " },
        ];

        
        this.inPerformanceData = [
          data["in"]["performance"],
          100 - data["in"]["performance"]
        ];

        this.barChartDataOut = [
          { data: [data["out"]["all"]], label: "  " },
          { data: [data["out"]["co"]], label: "" },
          { data: [data["out"]["betweenco"]], label: " " },
          { data: [data["out"]["mobile"]], label: "" }
        ];
        this.loadingData = false;
      },
      error => {
        this.loadingData = false;
        this.authServe.handdleAuthErrors(error);
      }
    );
  }
  public barChartOptions: any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels: string[] = ["  "];
  public detailPercentLabelsIn: string[] = ['   ','   ',' '];
  public detailPercentLabelsOut: string[] = ['  ',' ',' '];


  public barChartDataIn: any[] = [
    { data: [0], label: "  " },
    { data: [0], label: "   " },
    { data: [0], label: "   " },
    { data: [0], label: "" }
  ];
  public barChartDataTimeIn: any[] = [
    { data: [0], label: "   " },
    { data: [0], label: "    " },
  ];

  public barChartDataOut: any[] = [
    { data: [0], label: "  " },
    { data: [0], label: "" },
    { data: [0], label: " " },
    { data: [0], label: "" }
  ];
  public barChartDataTimeOut: any[] = [
    { data: [0], label: "   " },
    { data: [0], label: "    " },
  ];
}
