import { Component, OnInit, ViewChild } from "@angular/core";
import { DatatableComponent } from "@swimlane/ngx-datatable";
import { WebService } from "./web.service";
import { FormControl, FormGroup } from "@angular/forms";
import * as moment from "jalali-moment";
import { AuthenticationService } from "../../../../_services/authentication.service";
import { ViewEncapsulation } from "@angular/core";
import { SharedService } from "../../../../_services/shared.service";
import { DaterangeComponent } from "../_components/daterange/daterange.component";
import { Globals } from "../../../../_services/globals";
import { ToastrService } from "ngx-toastr";
@Component({
  selector: "app-operator",
  templateUrl: "./operator.component.html",
  styleUrls: ["./operator.component.scss"],
  encapsulation: ViewEncapsulation.None
})
export class OperatorComponent implements OnInit {
  constructor(
    private webService: WebService,
    private authServe: AuthenticationService,
    private sharedService: SharedService,
    private toaster: ToastrService
  ) {}

  public inPerformanceLabel: string[] = [""];
  public inPerformanceColors = [
    {
      backgroundColor: ["#20a8d8", "#eeeeee"]
    }
  ];

  public inPercentsColors = [
    {
      backgroundColor: ["#a5deb9", "#f86c6b", "#ffda6a"]
    }
  ];

  public inPerformanceData: number[] = [1, 100];

  public inDetailsPercent: number[] = [1, 0, 0];
  public outDetailsPercent: number[] = [1, 0, 0];

  allData: any = [];
  globData: any = [];

  @ViewChild("daterange") dateRange: DaterangeComponent;

  onSelectDate() {
    // this.updateCharts();
  }
  operators: any;
  mainDropdownSettings = {};
  ngOnInit() {
    this.webService.getAllOperator().subscribe(
      data => {
        this.operators = data;
      },
      error => {
        this.authServe.handdleAuthErrors(error);
      }
    );

    this.mainDropdownSettings = {
      singleSelection: false,
      idField: "id",
      textField: "name",
      selectAllText: " ",
      unSelectAllText: "  ",
      searchPlaceholderText: "",
      itemsShowLimit: 1,
      noDataAvailablePlaceholderText: " ",
      limitSelection: 1,
      allowSearchFilter: true
    };
  }

  operatorSelect: FormControl = new FormControl();
  loadingData = false;
  phoneNumber;
  data_login;
  data_logout;
  updateCharts() {
    this.loadingData = true;

    let data = {
      id: Globals.fetchData(this.operatorSelect.value, false),
      from: this.dateRange.selectedDateFrom.value,
      to: this.dateRange.selectedDateTo.value
    };

    if (!data.id) {
      this.loadingData = false;
      this.toaster.warning("   .");
      return;
    }

    this.webService.getOperatorData(data).subscribe(
      data => {
        data = data["data"];
        this.phoneNumber = data["phonenumber"];
        this.data_login = data["data_login"];
        this.data_logout = data["data_logout"];

        data = data["data"];
        this.barChartDataIn = [
          { data: [data["in"]["all"]], label: "  " },
          { data: [data["in"]["answer"]], label: "   " },
          { data: [data["in"]["noanswer"]], label: "   " },
          { data: [data["in"]["busy"]], label: " " }
        ];

        this.inDetailsPercent = [
          data["in"]["panswer"],
          data["in"]["pnoanswer"],
          data["in"]["pbusy"]
        ];
        this.outDetailsPercent = [
          data["out"]["pbetweenco"],
          data["out"]["pco"],
          data["out"]["pmobile"]
        ];

        this.barChartDataTimeIn = [
          { data: [data["in"]["time"]], label: "   " },
          { data: [data["in"]["avg"]], label: "    " }
        ];

        this.barChartDataTimeOut = [
          { data: [data["out"]["time"]], label: "   " },
          { data: [data["out"]["avg"]], label: "    " }
        ];

        this.inPerformanceData = [
          data["in"]["performance"],
          100 - data["in"]["performance"]
        ];

        this.barChartDataOut = [
          { data: [data["out"]["all"]], label: "  " },
          { data: [data["out"]["co"]], label: "" },
          { data: [data["out"]["betweenco"]], label: " " },
          { data: [data["out"]["mobile"]], label: "" }
        ];

        this.loadingData = false;
      },
      error => {
        this.loadingData = false;
        this.authServe.handdleAuthErrors(error);
      }
    );
  }

  public barChartOptions: any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels: string[] = ["  "];
  public detailPercentLabelsIn: string[] = [
    "   ",
    "   ",
    " "
  ];
  public detailPercentLabelsOut: string[] = ["  ", " ", " "];

  public barChartDataIn: any[] = [
    { data: [0], label: "  " },
    { data: [0], label: "   " },
    { data: [0], label: "   " },
    { data: [0], label: "" }
  ];
  public barChartDataTimeIn: any[] = [
    { data: [0], label: "   " },
    { data: [0], label: "    " }
  ];

  public barChartDataOut: any[] = [
    { data: [0], label: "  " },
    { data: [0], label: "" },
    { data: [0], label: " " },
    { data: [0], label: "" }
  ];
  public barChartDataTimeOut: any[] = [
    { data: [0], label: "   " },
    { data: [0], label: "    " }
  ];
}
